import { connect } from 'react-redux';
import { Link } from 'react-router';
import React from 'react';
import BaseComponent from 'client/BaseComponent';
import { updateUser, deleteUser } from 'client/actions/userDetail';

class User extends BaseComponent {
	
	updateUser() {
		this.props.updateUser();
	}
	
	deleteUser() {
		this.props.deleteUser();
	}
	
	render() {
		return (
			<div className="user">
				<div className="table">
					<dl className="tr">
						<dt className="td">{this.props.template.userName}</dt>
						<dd className="td">
							<Link to={"/user/"+this.props.user.id}>{this.props.user.name}</Link>
							<button type="button" className="right">update</button>
							<button type="button" className="right">delete</button>
						</dd>
					</dl>
					<dl className="tr">	
						<dt className="td">{this.props.template.userSex}</dt>
						<dd className="td">{this.props.user.sex}</dd>
					</dl>
					<dl className="tr">
						<dt className="td">{this.props.template.userEmail}</dt>
						<dd className="td">{this.props.user.email}</dd>
					</dl>
				</div>
			</div>
		);
	};
}

export default connect()(User);


