import BaseService from 'server/services/BaseService';

class ProfileService extends BaseService {
	
	static getInstance() {
		if(!this.profileService){
			this.profileService = new this;
		}
		
		return this.profileService;
	}
	
	constructor() {
		super();
		this.apiUrl = '/profile';
		this.profileService = null;
	}
	
	getProfile(params, callback) {
		let self = this;
		
		self.need(['i18n', 'rest']).then(function(i18n, rest){
			return [
				i18n(['profileTitle', 'userName', 'userSex', 'userEmail']),
				rest('get', 'http://10.102.5.108:4080/profile/' + params.pid, 'result'),
				rest('get', 'http://10.102.5.108:4080/profile/' + params.pid + '/exp', 'result'),
				rest('get', 'http://10.102.5.108:4080/profile/' + params.pid + '/edu', 'result')
			];
		}).end(callback);
	}
	
	getProfileList(params, callback) {
		let self = this;
		
		self.need(['i18n', 'rest']).then(function(i18n, rest){
			return [
				i18n(['profileTitle', 'userName', 'userSex', 'userEmail']),
				rest('get', 'http://10.102.5.108:4080/profile', 'result')
			];
		}).end(callback);
	}
	/*
	getDataDemo() {
		let self = this;
		
		self.need(['i18n', 'rest', 'soap']).then(function(i18n, rest, soap){
			return [
				i18n(['profileTitle', 'userName', 'userExp', 'userEdu', 'userWebsite', 'userNote']),
				rest('get', '/profile/' + params.pid, {}, 'result', (restResult) => {
					return [
						soap('/Experience.0.0', (client) => {
							client.queryAllExpData({'pid': restResult.pid, 'filter':'2', 'type':'0', 'source':'0'});
						}),
						soap('/Education.0.0', (client) => {
							client.queryAllEduData({'pid': restResult.pid, 'filter':'2', 'type':'0', 'source':'0'});
						})
					];
				}),
				rest('get', 'http://m.e104.com.tw/dev/_crab/api1.php', {}, 'result'),
				rest('get', 'http://m.e104.com.tw/dev/_crab/api2.php', {}, 'result')
			];
		}).end(function(results){
			callback({data:results[1],template:results[0]});
		});
	}
	*/
};

export default ProfileService;



