import { connect } from 'react-redux';
import { Link } from 'react-router';
import React from 'react';
import BaseComponent from 'client/BaseComponent';
import User from 'client/components/user';
import { loadUserList } from 'client/actions/userList';
import { addUser } from 'client/actions/userDetail';

class UserList extends BaseComponent {
	
	static fetchData({ params, store }) {
		return store.dispatch(loadUserList(params));
	}
	
	constructor(props, context){
		super(props, context);
	}
	
	componentDidMount(){
		this.props.loadUserList(this.props.paramMap);
	}
	
	addUser() {
		this.props.addUser();
	}
	
	render() {
		let userList = this.props.userList.map((user, index) => {
			return (
				<div className="user-list-item">
					<div className="item-options left">
						<input type="checkbox" />
					</div>
					<User key={index} user={user} template={this.props.template} />
				</div>
			);
		});
		
		return (
			<div className="user-list">
				<div className="title clearfix">
					<h1 className="left">UserList Page</h1>
					<button type="button" className="right">add</button>
					
				</div>
				<div className="user-list-item">
					<div className="item-options left">
						<button type="button" className="right">delete</button>
					</div>
					<div className="item-title"></div>
				</div>
				{userList}
			</div>
		);
	}
}

function mapStateToProps(state, props){
	let paramMap = Object.assign({}, props.params, props.location.query);
	return {
		userList: state.userList.data, template: state.userList.template, paramMap: paramMap
	};
}

export default connect(mapStateToProps, {loadUserList})(UserList);


