import { connect } from 'react-redux';
import { Link } from 'react-router';
import utils from 'lodash';
import React from 'react';
import BaseComponent from 'client/BaseComponent';
import User from 'client/components/user';
import { loadUser } from 'client/actions/userDetail';

class UserDetail extends BaseComponent {
	static fetchData({ params, store }) {
		return store.dispatch(loadUser(params));
	}
	constructor(props, context){
		super(props, context);
	}
	componentDidMount() {
		this.props.loadUser(this.props.paramMap);
	}
	render() {
		return (
			<div>
				<h2>{this.props.template.profileTitle}</h2>
				<User user={this.props.userDetail} template={this.props.template} />
				<Link to="/">Back to Home</Link>
			</div>
		);
	}
}

function mapStateToProps (state, props) {
	let paramMap = Object.assign({}, props.params, props.location.query);
	return { userDetail: state.userDetail.data, template: state.userDetail.template, paramMap: paramMap};
}

export { UserDetail };
export default connect(mapStateToProps, { loadUser })(UserDetail);


