import { CALL_API } from 'client/middleware/api';
import ProfileService from 'server/services/ProfileService';

const profileService = ProfileService.getInstance();

export const LOADED_USER = 'LOADED_USER';
export function loadUser(params) {
	return {
		[CALL_API]: {
			type: LOADED_USER,
			fe: (resolve)=>{
				resolve({
					method: 'get',
					apiUrl: profileService.apiUrl + '/' + params.pid,
					params: params
				})
			},
			be: (resolve) => {
				profileService.getProfile(params, (result) => {
					resolve(result);
				});
			}
		}
	};
}

