import Promise from 'bluebird';
import Superagent from 'superagent';

export const CALL_API = Symbol('CALL_API');
export default (store) => (next) => (action) => {
	if ( ! action[CALL_API] ) {
		return next(action);
	}
	
	let request = action[CALL_API];
	let type = request.type;
	let fe = request.fe;
	let be = request.be;
	let { getState } = store;
	
	return new Promise((resolve, reject) => {
		function backend(){
			try{
				be((response) => {
					next({
						type: type,
						response: response
					});
					
					resolve();
				});
			}catch(e){}
		}
		
		function frontend(frontendSetting){
			frontendSetting.method = frontendSetting.method.toLowerCase();
			frontendSetting.apiUrl = '/ajax' + frontendSetting.apiUrl;
			
			function responseCallback(e, response){
				next({
					type: type,
					response: response.body
				});
				
				resolve();
			}
			
			switch(frontendSetting.method){
				case 'get':
					Superagent.get(frontendSetting.apiUrl).end(responseCallback);
					break;
					
				case 'post':
					Superagent.post(frontendSetting.apiUrl).send(frontendSetting.params).end(responseCallback);
					break;
					
				case 'put':
					Superagent.put(frontendSetting.apiUrl).send(frontendSetting.params).end(responseCallback);
					break;
					
				case 'delete':
					Superagent.del(frontendSetting.apiUrl).end(responseCallback);
					break;
			}
		}
		
		function checkTodo(){
			// check for mode
			if(fe){
				// has fe setting, check for backend
				fe((testRestResponce) => {
					if(testRestResponce){
						if(testRestResponce.params && testRestResponce.params.serverSide === true){
							//backend
							backend();
							return;
						}else if(testRestResponce.method && testRestResponce.apiUrl){
							//frontend
							frontend(testRestResponce);
							return;
						}
					}
					
					// gg
				});
			}else{
				// not fe setting
				backend();
			}
		}
		
		checkTodo();
	});
};


