class BaseService {
	
	constructor() {
		try{
			let BackendService = require('server/services/BackendService');
			let backend = new BackendService();
			
			this.need = function(needAry){
				return backend.need(needAry);	
			};
		}catch(e){
			this.need = function(){return this;};
			this.then = function(){return this;};
			this.end = function(){};
		}
	}
};

export default BaseService;



