import { CALL_API } from 'client/middleware/api';
import ProfileService from 'server/services/ProfileService';

const profileService = ProfileService.getInstance();

export const LOADED_USERLIST = 'LOADED_USERLIST';
export function loadUserList(params) {
	return {
		[CALL_API]: {
			type: LOADED_USERLIST,
			fe: (resolve)=>{
				resolve({
					method: 'get',
					apiUrl: profileService.apiUrl,
					params: params
				})
			},
			be: (resolve) => {
				profileService.getProfileList(params, (result) => {
					resolve(result);
				});
			}
		}
	};
}


